@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}
body{font-family:"Roboto", sans-serif}.App{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);box-shadow:0px 1px 30px #757575;border-radius:10px}

.dropdown{max-width:320px;width:100%}.dropdown__wrapper{border-top-left-radius:10px;border-top-right-radius:10px}.dropdown .button__wrapper{position:relative}.dropdown__label{font-size:11px;padding:0px 4px;position:absolute;top:-2px;left:15px;color:#9761bb;background-color:#c3a2ee;z-index:5;transition:all ease 0.5s;font-weight:500}.dropdown__user{object-fit:cover;position:absolute;top:12px;left:15px;width:20px;height:20px;z-index:5}.dropdown__button{width:100%;border-radius:4px;padding:12px 28px 10px 15px;background-color:#c3a2ee;border:2px solid #9761bb;color:#222121;font-size:13px;font-family:"Roboto", sans-serif;font-weight:500;cursor:pointer;display:flex;align-items:center;justify-content:center;position:relative;transition:all ease 0.5s;margin-bottom:2px}.dropdown__button:focus,.dropdown__button:hover{border:2px solid #e4c6f8}.dropdown__button:hover+.dropdown__label,.dropdown__button:focus+.dropdown__label{color:#e4c6f8}.dropdown__button::before{position:absolute;content:"";border:5px solid transparent;border-top:5px solid #a545e6;top:17px;right:12px}.dropdown__list{background-color:#ffffff;border:1px solid #f3f2f2;border-radius:4px;font-size:14px;display:none;position:absolute;transition:all ease 0.3s}.dropdown__list.active{display:block;z-index:10;width:270px}.dropdown__list--item{padding:0 10px;color:#131414;width:100%;height:30px;cursor:pointer;letter-spacing:0.5px;display:flex;justify-content:center;align-items:center}.dropdown__list--item:first-child{margin-top:6px}.dropdown__list--item:last-child{margin-bottom:6px}.dropdown__list--item:hover{background-color:#eccefa}.dropdown .button__wrapper{position:relative}.dropdown .button__wrapper .user__image{width:20px;height:20px;object-fit:cover}

.inputs__list{padding:0px 25px;background-color:#ffffff;display:flex;justify-content:space-between;align-items:center;min-width:320px;width:100%;margin-bottom:15px}.inputs__list .input__wrapper{align-items:center;display:flex;justify-content:center;position:relative}.inputs__list .input__label{font-size:11px;padding:0px 3px;position:absolute;top:-2px;left:8px;font-weight:500;color:#9761bb;background-color:#ffffff;z-index:5;transition:all ease 0.5s}.inputs__list .input{border:2px solid #9761bb;width:90px;height:40px;border-radius:4px;text-align:center;font-size:13px;font-weight:500;padding-left:14px;font-family:"Roboto", sans-serif;cursor:pointer;transition:all ease 0.5s;outline:none}.inputs__list .input:focus,.inputs__list .input:hover{border:2px solid #cea6f7}.inputs__list .input:hover+.input__label,.inputs__list .input:focus+.input__label{color:#cea6f7}.inputs__list .input__salary{font-weight:500;font-size:13px}.dropdown__wrapper{background-color:#c3a2ee;padding:30px 25px 20px 25px;display:grid;grid-gap:15px;gap:15px;margin-bottom:14px}.wrapper{display:flex;justify-content:space-between;align-items:center}.total{padding:30px 20px;background-color:#c3a2ee;display:grid;grid-gap:20px;gap:20px;grid-template-columns:repeat(1, 150px);justify-content:center;align-items:center;border-bottom-left-radius:10px;border-bottom-right-radius:10px;font-family:"Roboto", sans-serif;font-size:14px;font-weight:500;color:#222121}.total__hours,.total__salary{display:flex;justify-content:space-between}.loading__image{object-fit:cover;width:35px;height:35px;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}

